.header {
	display: flex;
    height: 93px;
}
.headerGreen {
    display: flex;
    height: 93px;
    background-color: #029491;
}
.navLinks {
    display: flex;
    gap: 49px;
    margin-left: 401px;
    margin-top: 38px;
}
.imageScan {
    margin-left: 60px;;
}
.navLink {
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    color: #000000;
}
.navLink:hover {
	cursor: pointer;
	text-decoration: underline;
}
.regLink {
    font-size: 14px;
    line-height: 17px;
    opacity: 0.4;
    cursor: pointer;
    margin-left: 291px;
    margin-top: 37px;
}
.regLink:hover {
	cursor: pointer;
	text-decoration: underline;
}
.hr {
	width: 1px;
	height: 26px;
    background: #029491;
    opacity: 0.6;
    margin: 29px 0px 0px 18px;
}
.buttonLogin {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    background: #7CE3E1;
    border-radius: 5px;
    width: 65px;
    height: 26px;
    margin: 29px 0px 0px 20px;
    text-decoration: none;
    color: #000000;
    text-align: center;
	line-height: 26px;
}
.buttonLogin:hover {
	cursor: pointer;
	background: #0df7f3;
}
.navLogRegLinks {
    display: flex;
}
.personName {
    font-size: 14px;
    line-height: normal;
    opacity: 0.699999988079071;
}
.buttonLogout {color: #000;
    font-size: 10px;
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    opacity: 0.4000000059604645;
    border: none;
    text-align: right;
}
.buttonLogout:hover {
	cursor: pointer;
	opacity: 1;
    background-color: #FFF;
}
.personPhoto {
    border-radius: 50%;
    height: 40px;
    width: 40px;
    margin-top: 28px;
    margin-left: 5px;
}
.personWrapper {
    display: flex;
    flex-direction: column;
    margin-top: 30px;

}
.infoCompanyWrapper {
    display: flex;
    flex-direction: column;
    margin: 20px 144px 0px 138px;
    text-align: right;
}
.infoCompany  {
    font-size: 10px;
    line-height: normal;
    letter-spacing: 0.1px;
    opacity: 0.4000000059604645;
    margin: 0px;
}
.infoCompany:nth-child(1n) {
    margin-bottom: 7px;
}
.usedCompanyNumber {
    color: #000000;
    font-size: 14px;
    font-weight: 700;
    line-height: normal;
    margin-left: 9px;
}
.usedCompanyLimitNumber {
    font-size: 14px;
    font-weight: 700;
    line-height: normal;
    margin-left: 9px;
    color: #8AC540;
}
.loader {
    margin:30px 203px 0px 204px;
}
.mobileMenuIconClose {
    display: none;
}
.mobileMenuIconBar{
    display: none;
}
@media screen and (max-width: 780px) {
    .infoCompanyWrapper {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-left: 30px;
    text-align: left;
    }
    .infoCompany  {
        font-size: 8px;
    }
    .navLinks {
        display: none;  
    }
    .personWrapper {
        display: none;
    }
    .usedCompanyNumber {
        display: block;
        margin-left: 0px;
    }
    .usedCompanyLimitNumber {
        display: block;
        margin-left: 0px;
    }
    .buttonLogin {
       padding-left: 117px;
       padding-right: 117px;
       padding-top:15px;
       padding-bottom:12px;
       position: relative;
       top: 295px;
       left: -10px;
       margin-left: 0;
       font-size: 20px;
        line-height: normal;
        letter-spacing: 0.2px;
    }
    .regLink {
        font-size: 16px;
        line-height: normal;
        letter-spacing: 0.16px;
        position: relative;
        top: 240px;
        left: 220px;
        color: #FFF;
    }
    .buttonLogout {
        position: relative;
        top: 330px;
        right: 425px;
        padding-left: 117px;
        padding-right: 117px;
        font-size: 20px;
        padding-top:15px;
        padding-bottom:12px;
        border-radius: 5px;
    }
    .personPhoto {
        display: none;
    }
    .hr {
        display: none;
    }
    .personName {
        display: none;
    }
    .headerGreen {
        width: 375px;
        position: relative;
        z-index: 10;
    }
    .header {
        width: 375px;
        z-index: 10;
        position: relative;
    }
    .imageScan {
        margin-left: 14px;
        width: 111px;
        height: 90px;
    }
    .navLink{
        font-size: 16px;
        line-height: normal;
        letter-spacing: 0.16px;
        color: white;
        text-align: center;
        padding: 13px;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .navLink:first-child {
        padding-top: 45px;
    }
    .navLink:last-child {
        padding-bottom: 244px;
    }
    .navLinksMobile {
        position: absolute;
        display: block;
        list-style: none;
        background-color: #029491;
        top: 75px;
        width: 375px;
    }
    .navLinksLogRegMobile {
        position: absolute;
        top: 75px;
        right: 30px;
    }
    .navlinks {
        display: none;
    }
    .mobileMenuIconBar {
        display: block;
        position: absolute;
        font-size: 30px;
        color: #029491;
        background-color: white;
        border: none;
        outline: none;
        top: 20px;
        right: 20px;
    }
    .mobileMenuIconClose {
        display: block;
        position: absolute;
        font-size: 30px;
        color: white;
        background-color: #029491;
        border: none;
        outline: none;
        top: 20px;
        right: 20px;
    }
    .mobileMenuIconBar:hover, .mobileMenuIconClose:hover {
        cursor: pointer;
    }

}